.advantages {
  background-color: $primary-bg-color;

  @include mq(tablet) {
    display: flex;
  }
}
.advantages-list {
  text-align: center;
  @include mq(tablet) {
    display: flex;
  }
  &__item:not(:last-child) {
    @include mq(tablet) {
      margin-right: 80px;
    }
  }

  &__icon-block {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    background-color: $yellow-color;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    @include mq(tablet) {
      margin-bottom: 30px;
    }
    @include mq(desktop) {
      margin-bottom: 40px;
    }
  }

  &__icon {
    fill: black;
  }

  &__info {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.22;

    @include mq(tablet) {
      margin-bottom: 10px;
      font-size: 48px;
    }
    @include mq(desktop) {
      font-size: 58px;
    }
  }
}
